@import '../../styles/extends';

.homePage__container {
  padding: 25px 0 0;
  min-width: 320px;
  height: 100%;

  @include onTablet {
    padding: 25px 0 0;
    min-width: 640px;
  }

  @include onLaptop {
    min-width: 768px;
  }

  @include onDesktop {
    padding: 25px 32px 0 32px;
    width: 1200px;
  }
}
